<template>
    <section
        v-if="waps"
        class="backoffice-grid-msgs"
    >
        <div
            v-for="(wap) in waps"
            :key="wap._id"
        >
            <ul
                class="clean-list"
                v-for="msg in wap.reviews"
                :key="msg._id"
            >
                <li class="backoffice-card flex space-between">
                    <button class="btn remove-card"><i class="fas fa-times-circle"></i></button>
                    <div class="info flex column">
                        <p class="wap-name">Website Name :{{wap.title || 'Webcraft website'}}</p>
                        <span class="created-at">{{formatTime(msg.content.createdAt)}}</span>
                        <span>{{msg.content.byUser.username || 'Guest'}} Message:</span>
                        <h1>Message by : {{msg.content.name||'No name'}}</h1>
                        <p>Email: {{msg.content.email||'No name'}}</p>
                        <p>Subject: {{msg.content.subject||'No name'}}</p>
                    </div>
                    <img
                        class="wap-thumbnail"
                        :src="imgUrl(wap.thumbnail)"
                    >
                    <button
                        class="btn check-website"
                        @click="pushToWap(wap._id)"
                    >Check Website</button>
                </li>

            </ul>
        </div>
    </section>
</template>

<script>
export default {
    name: 'backoffice-msg',
    props: {
        waps: {
            type: Array
        },
    },
    methods: {
        formatTime(time) {
            return new Date(time).toLocaleString()
        },
        pushToWap(wapId) {
            window.open(`https://webcraft-ca.herokuapp.com/#/wap/${wapId}`)
        },
        imgUrl(thumbnail) {
            return thumbnail? thumbnail:'data:image/png;base64,'
        }

    }
}



</script>

